body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Background {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  color: white;
}

.BackButton {
  display: flex;
  padding-left: 5px;
  height: 5vh;
  align-items: center;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
}

.Container {
  background-color: #4e5563;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border-radius: 10%;
}

.App a {
  color: #61dafb;
  margin-bottom: 10px;
  text-decoration: none;
}

.title {
  font-size: 3rem;
  margin-bottom: 10px;
}

.mobileOptionContainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.mobileOption {
  text-align: center;
  align-self: center;
}

.Board {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.BetBoard {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.Result {
  color: #61dafb;
  flex-direction: column;
  display: flex;
  align-items: center;
}

.Group {
  flex-direction: column;
  display: flex;
  align-items: center;
}

.CardContainer {
  display: flex;
  justify-content: center;
  height: 30vh;
  max-width: 100vw;
  white-space: nowrap;
}

.ImageContainer {
  max-width: 100%;
  max-height: 100%;
}

.Board img {
  max-width: 100%;
  max-height: 100%;
}

.BetOptions {
  height: 60vh;
  width: 100vw;
  display: grid;
  /* grid-template-columns: auto auto auto; */
  /* grid-template-rows: auto auto auto; */
  align-items: center;
  justify-content: center;
}

.MoneyBet {
  color: #61dafb;
  flex-direction: column;
  display: flex;
  align-items: center;
}

.BetConfirmation {
  flex-direction: column;
  display: flex;
  align-items: center;
}

.Gameover {
  position: absolute;
  flex-direction: column;
  margin-top: 5vh;
  height: 95vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #61dafb;
  font-size: 10rem;
  background-color: rgba(52, 52, 52, 0.8);
}

